"use client";

import { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function Home() {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [selectedMethod, setSelectedMethod] = useState('SAW');
  const [schedule, setSchedule] = useState<any[]>([]);
  const [stats, setStats] = useState({ students: 0, examiners: 0, rooms: 0, timeslots: 0 });

  const API_BASE = 'http://localhost:8001';

  const showError = (message: string) => {
    setError(message);
    setTimeout(() => setError(''), 5000);
  };

  const showSuccess = (message: string) => {
    setSuccess(message);
    setTimeout(() => setSuccess(''), 5000);
  };

  const loadSampleDataAndGenerate = async () => {
    setLoading(true);
    try {
      // Sample data
      const sampleData = {
        students: [
          { id: 1, name: "Andi Pratama", nim: "20190001", thesis_title: "Implementasi Machine Learning untuk Prediksi Cuaca", thesis_field: "machine learning artificial intelligence data science", supervisor_id: 1, gpa: 3.75, thesis_quality: 4.2 },
          { id: 2, name: "Sari Dewi", nim: "20190002", thesis_title: "Pengembangan Aplikasi Web E-Commerce dengan React", thesis_field: "web development software engineering frontend", supervisor_id: 2, gpa: 3.60, thesis_quality: 4.0 },
          { id: 3, name: "Budi Santoso", nim: "20190003", thesis_title: "Sistem Keamanan Jaringan menggunakan Blockchain", thesis_field: "network security blockchain cybersecurity", supervisor_id: 3, gpa: 3.85, thesis_quality: 4.5 },
          { id: 4, name: "Maya Sari", nim: "20190004", thesis_title: "Analisis Big Data untuk Business Intelligence", thesis_field: "big data analytics business intelligence data mining", supervisor_id: 4, gpa: 3.70, thesis_quality: 4.1 },
          { id: 5, name: "Rudi Hermawan", nim: "20190005", thesis_title: "Pengembangan Game Mobile dengan Unity", thesis_field: "game development mobile programming unity", supervisor_id: 5, gpa: 3.55, thesis_quality: 3.8 }
        ],
        examiners: [
          { id: 1, name: "Prof. Dr. Ahmad Wijaya, M.Kom", title: "Professor", expertise: ["machine learning", "artificial intelligence", "data science", "computer vision"], experience_years: 15, workload: 2, availability_score: 4.0, competency_score: 4.8, available_timeslots: [1, 2, 3, 4] },
          { id: 2, name: "Dr. Rina Kusuma, M.T", title: "Associate Professor", expertise: ["web development", "software engineering", "frontend development", "javascript"], experience_years: 12, workload: 3, availability_score: 4.2, competency_score: 4.5, available_timeslots: [2, 3, 4, 5] },
          { id: 3, name: "Dr. Bambang Sutrisno, M.Kom", title: "Associate Professor", expertise: ["network security", "cybersecurity", "blockchain", "cryptography"], experience_years: 10, workload: 1, availability_score: 4.5, competency_score: 4.6, available_timeslots: [1, 3, 5, 6] },
          { id: 4, name: "Dr. Indira Sari, M.Sc", title: "Assistant Professor", expertise: ["big data", "data analytics", "business intelligence", "data mining"], experience_years: 8, workload: 2, availability_score: 4.3, competency_score: 4.4, available_timeslots: [3, 4, 5, 6] },
          { id: 5, name: "Dr. Fajar Nugroho, M.T", title: "Assistant Professor", expertise: ["game development", "mobile programming", "unity", "computer graphics"], experience_years: 7, workload: 1, availability_score: 4.7, competency_score: 4.2, available_timeslots: [1, 2, 4, 6] }
        ],
        rooms: [
          { id: 1, name: "Ruang Sidang A", capacity: 25, facilities: ["projector", "whiteboard", "sound_system", "air_conditioning"], location: "Gedung Informatika Lt. 3", quality_score: 4.5 },
          { id: 2, name: "Ruang Sidang B", capacity: 20, facilities: ["projector", "whiteboard", "air_conditioning"], location: "Gedung Informatika Lt. 3", quality_score: 4.0 },
          { id: 3, name: "Ruang Sidang C", capacity: 15, facilities: ["projector", "whiteboard"], location: "Gedung Informatika Lt. 2", quality_score: 3.5 },
          { id: 4, name: "Ruang Seminar", capacity: 30, facilities: ["projector", "whiteboard", "sound_system", "air_conditioning", "microphone"], location: "Gedung Informatika Lt. 4", quality_score: 4.8 }
        ],
        timeslots: [
          { id: 1, day: "Senin", start_time: "08:00", end_time: "10:00", session: "Pagi" },
          { id: 2, day: "Senin", start_time: "10:30", end_time: "12:30", session: "Pagi" },
          { id: 3, day: "Senin", start_time: "13:30", end_time: "15:30", session: "Siang" },
          { id: 4, day: "Selasa", start_time: "08:00", end_time: "10:00", session: "Pagi" },
          { id: 5, day: "Selasa", start_time: "10:30", end_time: "12:30", session: "Pagi" },
          { id: 6, day: "Selasa", start_time: "13:30", end_time: "15:30", session: "Siang" }
        ]
      };

      const examSessions = sampleData.students.map(student => ({
        id: student.id,
        student_id: student.id,
        duration: 120,
        required_examiners: 3,
        priority: 1.0
      }));

      const response = await fetch(`${API_BASE}/schedule`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...sampleData,
          exam_sessions: examSessions,
          method: selectedMethod
        })
      });

      if (!response.ok) {
        throw new Error('Gagal membuat jadwal');
      }

      const result = await response.json();
      setSchedule(result.schedule);
      setStats({
        students: sampleData.students.length,
        examiners: sampleData.examiners.length,
        rooms: sampleData.rooms.length,
        timeslots: sampleData.timeslots.length
      });
      
      showSuccess(`Jadwal berhasil dibuat dengan metode ${selectedMethod}! Total kepuasan: ${result.total_satisfaction.toFixed(3)}`);
      setActiveTab('schedule');
    } catch (err) {
      showError('Gagal membuat jadwal: ' + (err as Error).message);
    } finally {
      setLoading(false);
    }
  };

  const getTimeslotInfo = (timeslotId: number) => {
    const timeslots = [
      { id: 1, day: "Senin", start_time: "08:00", end_time: "10:00" },
      { id: 2, day: "Senin", start_time: "10:30", end_time: "12:30" },
      { id: 3, day: "Senin", start_time: "13:30", end_time: "15:30" },
      { id: 4, day: "Selasa", start_time: "08:00", end_time: "10:00" },
      { id: 5, day: "Selasa", start_time: "10:30", end_time: "12:30" },
      { id: 6, day: "Selasa", start_time: "13:30", end_time: "15:30" }
    ];
    const timeslot = timeslots.find(t => t.id === timeslotId);
    return timeslot ? `${timeslot.day}, ${timeslot.start_time}-${timeslot.end_time}` : 'Unknown';
  };

  const getRoomName = (roomId: number) => {
    const rooms = [
      { id: 1, name: "Ruang Sidang A" },
      { id: 2, name: "Ruang Sidang B" },
      { id: 3, name: "Ruang Sidang C" },
      { id: 4, name: "Ruang Seminar" }
    ];
    const room = rooms.find(r => r.id === roomId);
    return room ? room.name : 'Unknown';
  };

  const getExaminerNames = (examinerIds: number[]) => {
    const examiners = [
      { id: 1, name: "Prof. Dr. Ahmad Wijaya, M.Kom" },
      { id: 2, name: "Dr. Rina Kusuma, M.T" },
      { id: 3, name: "Dr. Bambang Sutrisno, M.Kom" },
      { id: 4, name: "Dr. Indira Sari, M.Sc" },
      { id: 5, name: "Dr. Fajar Nugroho, M.T" }
    ];
    return examinerIds.map(id => {
      const examiner = examiners.find(e => e.id === id);
      return examiner ? examiner.name : 'Unknown';
    });
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container mx-auto p-6">
        <div className="mb-8 text-center">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">
            Sistem Pendukung Keputusan Penjadwalan Ujian Skripsi
          </h1>
          <p className="text-lg text-gray-600">
            Menggunakan Metode MCDM (AHP, SAW, TOPSIS)
          </p>
        </div>

        {error && (
          <Alert className="mb-4 border-red-200 bg-red-50">
            <AlertDescription className="text-red-800">{error}</AlertDescription>
          </Alert>
        )}

        {success && (
          <Alert className="mb-4 border-green-200 bg-green-50">
            <AlertDescription className="text-green-800">{success}</AlertDescription>
          </Alert>
        )}

        <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="dashboard">Dashboard</TabsTrigger>
            <TabsTrigger value="about">Tentang Sistem</TabsTrigger>
            <TabsTrigger value="schedule">Jadwal Ujian</TabsTrigger>
          </TabsList>

          <TabsContent value="dashboard" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium">Mahasiswa</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.students}</div>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium">Penguji</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.examiners}</div>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium">Ruangan</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.rooms}</div>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium">Slot Waktu</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.timeslots}</div>
                </CardContent>
              </Card>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>Buat Jadwal Ujian</CardTitle>
                <CardDescription>
                  Pilih metode MCDM dan buat jadwal ujian optimal dengan data contoh
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex gap-4 items-center">
                  <Label htmlFor="method">Metode MCDM:</Label>
                  <Select value={selectedMethod} onValueChange={setSelectedMethod}>
                    <SelectTrigger className="w-64">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="SAW">SAW (Simple Additive Weighting)</SelectItem>
                      <SelectItem value="TOPSIS">TOPSIS</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div className="flex gap-4">
                  <Button onClick={loadSampleDataAndGenerate} disabled={loading} className="bg-blue-600 hover:bg-blue-700">
                    {loading ? 'Membuat Jadwal...' : 'Buat Jadwal dengan Data Contoh'}
                  </Button>
                </div>
                <div className="text-sm text-gray-600">
                  <p>Sistem akan menggunakan data contoh yang sudah tersedia:</p>
                  <ul className="list-disc list-inside mt-2 space-y-1">
                    <li>5 Mahasiswa dengan berbagai bidang skripsi</li>
                    <li>5 Penguji dengan keahlian yang beragam</li>
                    <li>4 Ruangan dengan fasilitas berbeda</li>
                    <li>6 Slot waktu (Senin-Selasa)</li>
                  </ul>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="about" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Tentang Sistem</CardTitle>
                <CardDescription>
                  Sistem Pendukung Keputusan untuk Penjadwalan Ujian Skripsi
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <h3 className="font-semibold mb-2">Metode MCDM yang Digunakan:</h3>
                  <div className="space-y-3">
                    <div className="border-l-4 border-blue-500 pl-4">
                      <h4 className="font-medium">SAW (Simple Additive Weighting)</h4>
                      <p className="text-sm text-gray-600">Metode penjumlahan terbobot sederhana yang mudah dipahami dan diimplementasikan.</p>
                    </div>
                    <div className="border-l-4 border-green-500 pl-4">
                      <h4 className="font-medium">TOPSIS</h4>
                      <p className="text-sm text-gray-600">Technique for Order Preference by Similarity to Ideal Solution - berdasarkan konsep solusi ideal.</p>
                    </div>
                    <div className="border-l-4 border-purple-500 pl-4">
                      <h4 className="font-medium">AHP (Analytic Hierarchy Process)</h4>
                      <p className="text-sm text-gray-600">Proses hierarki analitik untuk perbandingan berpasangan dan perhitungan bobot kriteria.</p>
                    </div>
                  </div>
                </div>

                <div>
                  <h3 className="font-semibold mb-2">Kriteria Evaluasi:</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Badge variant="outline" className="w-full justify-start">Expertise Match (30%)</Badge>
                      <Badge variant="outline" className="w-full justify-start">Competency Score (25%)</Badge>
                      <Badge variant="outline" className="w-full justify-start">Availability Score (20%)</Badge>
                    </div>
                    <div className="space-y-2">
                      <Badge variant="outline" className="w-full justify-start">Workload (15%)</Badge>
                      <Badge variant="outline" className="w-full justify-start">Experience Years (10%)</Badge>
                    </div>
                  </div>
                </div>

                <div>
                  <h3 className="font-semibold mb-2">Fitur Sistem:</h3>
                  <ul className="list-disc list-inside space-y-1 text-sm text-gray-600">
                    <li>Evaluasi penguji otomatis berdasarkan multiple criteria</li>
                    <li>Penjadwalan optimal tanpa konflik waktu dan ruangan</li>
                    <li>Analisis kualitas jadwal yang dihasilkan</li>
                    <li>Interface yang user-friendly untuk pengguna awam</li>
                    <li>Backend API yang robust dan scalable</li>
                  </ul>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="schedule" className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Jadwal Ujian Skripsi</CardTitle>
                <CardDescription>
                  Hasil penjadwalan menggunakan metode {selectedMethod}
                </CardDescription>
              </CardHeader>
              <CardContent>
                {schedule.length === 0 ? (
                  <div className="text-center py-8">
                    <p className="text-gray-500">Belum ada jadwal yang dibuat.</p>
                    <p className="text-sm text-gray-400 mt-2">Klik "Buat Jadwal dengan Data Contoh" di tab Dashboard untuk memulai.</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {schedule.map((exam, index) => (
                      <div key={index} className="border rounded-lg p-4 bg-white">
                        <div className="flex justify-between items-start mb-3">
                          <div>
                            <h3 className="font-semibold text-lg">{exam.student_name}</h3>
                            <p className="text-sm text-gray-600">Ujian #{exam.exam_id}</p>
                          </div>
                          <Badge variant="outline" className="bg-green-50">
                            Skor: {exam.total_score.toFixed(3)}
                          </Badge>
                        </div>
                        
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
                          <div>
                            <p className="font-medium text-gray-700">Waktu:</p>
                            <p>{getTimeslotInfo(exam.timeslot_id)}</p>
                          </div>
                          <div>
                            <p className="font-medium text-gray-700">Ruangan:</p>
                            <p>{getRoomName(exam.room_id)}</p>
                          </div>
                          <div>
                            <p className="font-medium text-gray-700">Penguji:</p>
                            <div className="space-y-1">
                              {getExaminerNames(exam.assigned_examiners).map((name, idx) => (
                                <p key={idx} className="text-xs bg-gray-100 px-2 py-1 rounded">{name}</p>
                              ))}
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
